@import "../../../colors";

.high-score-container { 
    height: 400px; 
    width: 300px;
    margin: 20px;
    padding: 10px;
    border-radius: 18px;
    .high-score-data { 
        overflow-x: auto;
        height: 312px; 
        width: 100%; 
        .high-score-item { 
            display: flex; 
            justify-content: space-between; 
            align-items: center; 
            margin-right: 5px;
            border-bottom: 1px solid black;
        }
    }
}

